.podcast-card {
    width: 200px;
    min-height: 80px;
    background:   linear-gradient(218deg, rgb(37 47 50 / 50%) 7%, #1a2427 49%, rgb(0 0 0) 92%);
   
    border-radius: 0.5rem; 
     padding: 0.5rem; 
     /* padding-bottom: 2rem; */
    opacity: 0.7;
    cursor: pointer;
  }
  
  .display-image-podcast {
    width: 100%;
    height: 150px;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
  }
  
  .podcast-card:hover {
    opacity: 1;
    transition: all 0.3s;
    scale : 105%;
  }
  
  .title-podcast {
    color: var(--white) !important;
    text-decoration: none !important;
    margin: 1rem 0.4rem;
    font-size: 16px;
    font-weight:500;
  }
  
  .title-podcast-content{
  
  
    color :lightslategray !important;
    
    margin: 1rem 0.4rem;
    font-size: 12px;
    font-weight:600;
    text-transform: uppercase;
  }