.podcast-card-mediumcard {
    width: 150px;
    min-height: 60px;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    /* background:   linear-gradient(-80deg, rgba(3,27,33,1) 7%, rgba(21,107,131,1) 49%, rgba(3,32,40,1) 92%); */
  
    border-radius: 0.5rem;
    padding: 0.5rem;
    /* padding-bottom: 2rem; */
    opacity: 0.7;
    cursor: pointer;
  }
  
  .display-image-podcast-mediumcard {
    width: 100%;
    height: 150px;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
  }
  
  .podcast-card-mediumcard:hover {
    opacity: 1;
    transition: all 0.3s;
    scale: 105%;
  }
  
  .title-podcast-mediumcard {
    color: var(--white) !important;
    text-decoration: none !important;
    margin: 1rem 0.4rem;
    font-size: 16px;
    font-weight: 500;
  }
  
  .title-podcast-content-mediumcard {
    color: lightslategray !important;
  
    margin: 1rem 0.4rem;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
  }
  
  @media screen and (max-width: 640px) {
    .podcast-card-mediumcard {
      width: 100px;
    }
  
    .podcast-card-mediumcard > img {
      height: auto;
    }
  }